<template>
   <transition name="detailMove">
        <div class="detail" v-show="detailShow">
            <div class="detail-wrapper">
                <h1>{{article.detail.title}}</h1>
                <div class="date">{{article.detail.date}}</div>
                <div class="pubPerson">发布人：{{article.detail.pubPerson}}</div>
                <div class="artlinel"><img src="https://8283792.github.io/fx/images/artlinel.jpg" alt=""></div>
                <div class="subhead">{{article.detail.intro}}</div>
                <div class="artliner"><img src="https://8283792.github.io/fx/images/artliner.jpg" alt=""></div>
                <div style="clear: both"></div>
                <div class="art-cont">
                    <h3>{{article.detail.h3}}</h3>
                    <p v-for="p in article.detail.p1">{{p}}</p>
                    <h4>{{article.detail.h4}}</h4>
                    <img class="img1" :src="article.detail.img1" alt="">
                    <p v-for="p in article.detail.p2">{{p}}</p>
                    <img class="img1" :src="article.detail.img2" alt="">
                    <p v-for="p in article.detail.p3">{{p}}</p>
                    <img :src="article.detail.img3" class="img1" alt="">
                    <div v-for="example in article.detail.examples" class="example">
                        <h4 class="h4">{{example.h4}}</h4>
                        <p v-for="p in example.p">{{p}}</p>
                    </div>
                    <img :src="article.detail.img4" class="img1" alt="">
                    <div v-for="example in article.detail.examples2" class="example">
                        <h4 class="h4">{{example.h4}}</h4>
                        <p v-for="p in example.p">{{p}}</p>
                    </div>
                    <img width="500" height="333" class="img1" :src="article.detail.img5" alt="">
                    <div v-for="example in article.detail.examples3" class="example">
                        <h4 class="h4">{{example.h4}}</h4>
                        <p v-for="p in example.p">{{p}}</p>
                    </div>
                    <div class="summary">
                        <p v-for="summary in article.detail.summarys">{{summary}}</p>
                    </div>
                </div>
                <footer>
                    <div class="from">{{article.bottom.from}}</div>
                    <div><img class="QRcode" :src="article.bottom.QRcode" alt=""></div>
                    <div><img class="zan" :src="article.bottom.zan" alt=""></div>
                </footer>
            </div>
        </div>
   </transition>
</template>

<script>
export default {
  name: 'detail',
  props: {
    article: {
      type: Object
    }
  },
  data () {
    return {
      detailShow: false
    }
  },
  methods: {
    showDetail () {
      this.detailShow = true
    }
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
@import './detail.styl'
</style>



