<template>
    <div class="all">
        <div ref="artWrapper" class="all-wrapper">
            <ul ref="artCont">
                <li class="artItem" v-for="article in articles">
                    <div class="all-wrapper-left">
                        <h2 @click.prevent="showDetail(article)" class="art-title">{{article.title}}</h2>
                        <div class="mesDis">
                            <a href=""><img width="20" src="https://8283792.github.io/fx/images/iconeye.png" alt="">{{article.view}}</a>
                            <a href=""><img width="20" src="https://8283792.github.io/fx/images/iconstar.png" alt="">{{article.collect}}</a>
                            <span>{{article.data}}</span>
                        </div>
                    </div>
                    <div class="all-wrapper-right">
                        <a href=""><img :src="article.img" width="100%" height="80"></a>
                    </div>
                </li>
            </ul>
        </div>
        <detail :article="article" ref="detail"></detail>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import detail from '../../components/detail/detail'
export default {
  name: 'all',
  props: {
    data: {
      type: Object
    }
  },
  created () {
    this.$nextTick(() => {
      if (!this.scroll) {
        this._initLi()
      } else {
        this.scroll.refresh()
      }
    })
  },
  watch: {
    // tabs添加变化时执行
    'articles' () {
      this._initLi()
    }
  },
  methods: {
    _initLi () {
      let liH = 115
      let height = liH * this.articles.length
      this.$refs.artCont.style.height = height + 'px'
      this.scroll = new BScroll(this.$refs.artWrapper, {click: true})
    },
    showDetail (article) {
      this.$refs.detail.showDetail()
      this.article = article
    }
  },
  data () {
    return {
      articles: this.data.articles,
      article: {}
    }
  },
  components: {
    detail
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
</style>



