import Vue from 'vue'
import Router from 'vue-router'
import all from '../components/all/all'
import travel from '../components/travel/travel'
import life from '../components/life/life'
import circle from '../components/circle/circle'
import cate from '../components/cate/cate'
import joke from '../components/joke/joke'
import Cutebaby from '../components/Cutebaby/Cutebaby'
import sport from '../components/sport/sport'
import worthy from '../components/worthy/worthy'
import recruit from '../components/recruit/recruit'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      redirect: all
    },
    {
      path: '/all',
      name: 'all',
      component: all
    },
    {
      path: '/travel',
      name: 'travel',
      component: travel
    },
    {
      path: '/life',
      name: 'life',
      component: life
    },
    {
      path: '/circle',
      name: 'circle',
      component: circle
    },
    {
      path: '/cate',
      name: 'cate',
      component: cate
    },
    {
      path: '/joke',
      name: 'joke',
      component: joke
    },
    {
      path: '/Cutebaby',
      name: 'Cutebaby',
      component: Cutebaby
    },
    {
      path: '/sport',
      name: 'sport',
      component: sport
    },
    {
      path: '/worthy',
      name: 'worthy',
      component: worthy
    },
    {
      path: '/recruit',
      name: 'recruit',
      component: recruit
    }
  ],
  linkActiveClass: 'active'
})



