<template>
    <div class="head">
        <div class="head-wrapper">
            <span @click.prevent="showDetail"><img class="back-img" src="https://8283792.github.io/fx/images/iconhead.png" /></span>
            <a href=""><img class="logo" src="https://8283792.github.io/fx/images/logo.png" /></a>
            <span class="head-left">
                <span @click="searchDetail=!searchDetail"><img src="https://8283792.github.io/fx/images/iconsearch.png" /></span>
                <span @click="channelDetail=!channelDetail"><img src="https://8283792.github.io/fx/images/iconmore.png" /></span>
            </span>
        </div>
        <transition name="nav-left-move">
            <div v-show="detailShow" class="nav-lf">
                <div class="nav-lf-btn">
                    <span class="nav-lf-btn2" @click="hideDetail"><img src="https://8283792.github.io/fx/images/arr.png" /></span>
                </div>
                <div class="nav-lf-ct">
                    <a href="">首页</a>
                </div>
            </div>
        </transition>
        <transition name="search-move">
            <div v-show="searchDetail" class="search">
                <div class="search-body">
                    <input type="text" class="search-inpt" placeholder="请输入关键字">
                    <button type="submit" class="search-submit">
                        <img class="search-submit-img" src="https://8283792.github.io/fx/images/iconsearchb.png" />
                    </button>
                </div>
                <div class="search-hot-wrapper">
                    <a class="search-hot" v-for="hot in hots" href="">{{hot}}</a>
                </div>
            </div>
        </transition>
        <transition name="channel-move">
            <div v-show="channelDetail" class="channel" @click="hideNav">
                <h2>频道</h2>
                <div class="channel-wrapper">
                  <router-link to="/all">全部</router-link>
                  <router-link to="/travel">旅行</router-link>
                  <router-link to="/life">生活</router-link>
                  <router-link to="/circle">圈子</router-link>
                  <router-link to="/cate">美食</router-link>
                  <router-link to="/joke">笑话</router-link>
                  <router-link to="/Cutebaby">萌宝</router-link>
                  <router-link to="/sport">运动</router-link>
                  <router-link to="/worthy">贤人</router-link>
                  <router-link to="/recruit">招聘</router-link>
                </div>
                <h2>推荐</h2>
                <div class="recommend-wrapper">
                  <router-link to="/travel">旅行</router-link>
                  <router-link to="/cate">美食</router-link>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import data from '../../assets/data.json'
export default {
  name: 'head',
  data () {
    return {
      detailShow: false,
      searchDetail: false,
      channelDetail: false,
      hots: data.hot
    }
  },
  methods: {
    showDetail () {
      this.detailShow = true
    },
    hideDetail () {
      this.detailShow = false
    },
    hideNav () {
      this.channelDetail = false
    },
    esc () {
      this.detailShow = false
      this.searchDetail = false
      this.channelDetail = false
    }
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
@import './head.styl'
</style>



