<template>
  <div id="app">
    <div @click.stop="prevD">
      <v-head ref="vhead"></v-head>
    </div>
    <tab :tabs="data.tabs"></tab>
    <div @click.prevent.stop="esc($event)">
      <transition name="artMove">
        <router-view class="routerCont" :data="data"></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
import data from './assets/data.json'

import vhead from './components/head/head'
import tab from './components/tab/tab'
export default {
  name: 'app',
  data () {
    return {
      data,
      articles: data.articles,
      num: 1
    }
  },
  components: {
    'v-head': vhead,
    tab
  },
  methods: {
    esc (event) {
      this.$refs.vhead.esc()
      if (this.num === 1) {
        // 恢复默认事件
        window.event.returnValue = true
        return
      }
      this.num = 1
    },
    prevD () {
      this.num = null
    }
  }
}
</script>
<style lang="stylus" style="stylesheet/stylus">
@import './common/css/article.styl'
.active
  color: red
</style>



