<template>
    <div class="tab" ref="tabScroll">
        <ul ref="tabLi">
            <li>
                <router-link to="/all">{{tabs[0]}}</router-link>
            </li>
            <li>
                <router-link to="/travel">{{tabs[1]}}</router-link>
            </li>
            <li>
                <router-link to="/life">{{tabs[2]}}</router-link>
            </li>
            <li>
                <router-link to="/circle">{{tabs[3]}}</router-link>
            </li>
            <li>
                <router-link to="/cate">{{tabs[4]}}</router-link>
            </li>
            <li>
                <router-link to="/joke">{{tabs[5]}}</router-link>
            </li>
            <li>
                <router-link to="/Cutebaby">{{tabs[6]}}</router-link>
            </li>
            <li>
                <router-link to="/sport">{{tabs[7]}}</router-link>
            </li>
            <li>
                <router-link to="/worthy">{{tabs[8]}}</router-link>
            </li>
            <li>
                <router-link to="/recruit">{{tabs[9]}}</router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'tab',
  data () {
    return {
    }
  },
  created () {
    this.$nextTick(() => {
      if (!this.scroll) {
        this._initLi()
      } else {
        this.scroll.refresh()
      }
    })
  },
  props: {
    tabs: {
      type: Array
    }
  },
  watch: {
    // tabs添加变化时执行
    'tabs' () {
      this._initLi()
    }
  },
  methods: {
    _initLi () {
      let liW = 32
      let margin = 18
      let width = (liW + margin) * this.tabs.length
      this.$refs.tabLi.style.width = width + 'px'
      this.scroll = new BScroll(this.$refs.tabScroll, {scrollX: true, eventPassthrough: 'vertical'})
    }
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
@import './tab.styl'
</style>



