<template>
    <div class="all">
        <div ref="artWrapper" class="all-wrapper">
            <ul ref="artCont">
                <li class="artItem" v-for="CutebabyArt in CutebabyArts">
                    <div class="all-wrapper-left">
                        <h2><a class="art-title" href="">{{CutebabyArt.title}}</a></h2>
                        <h3><a class="art-subhead" href="">{{CutebabyArt.subhead}}</a></h3>
                        <div class="mesDis">
                            <a href=""><img width="20" src="https://8283792.github.io/fx/images/iconeye.png" alt="">{{CutebabyArt.view}}</a>
                            <a href=""><img width="20" src="https://8283792.github.io/fx/images/iconstar.png" alt="">{{CutebabyArt.collect}}</a>
                            <span>{{CutebabyArt.date}}</span>
                        </div>
                    </div>
                    <div class="all-wrapper-right">
                        <a href=""><img :src="CutebabyArt.img" width="100%" height="80"></a>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'all',
  props: {
    data: {
      type: Object
    }
  },
  created () {
    this.$nextTick(() => {
      if (!this.scroll) {
        this._initLi()
      } else {
        this.scroll.refresh()
      }
    })
  },
  watch: {
    // tabs添加变化时执行
    'CutebabyArts' () {
      this._initLi()
    }
  },
  methods: {
    _initLi () {
      let height = null
      let _liHObj = document.querySelectorAll('.artItem')
      let len = _liHObj.length
      if (len > this.CutebabyArts.length) {
        let num = len - this.CutebabyArts.length
        for (let i = num; i < len; i++) {
          height += _liHObj[i].offsetHeight + 11
        }
      } else {
        for (let i = 0; i < len; i++) {
          height += _liHObj[i].offsetHeight + 11
        }
      }
      this.$refs.artCont.style.height = height + 'px'
      this.scroll = new BScroll(this.$refs.artWrapper, {click: true})
    }
  },
  data () {
    return {
      CutebabyArts: this.data.CutebabyArts
    }
  }
}
</script>

<style lang="stylus" style="stylesheet/stylus">
</style>



